import React from 'react'

export default function WorkCard(props) {
    return (
        <div className="cont">
            <div className="left">
                <div className="overlay">
                    <img src="/assets/icons8-zoom-in-64.png" alt="search icon" />
                </div>
                <img className="image" alt={props.data.title} src={props.data.image} />
            </div>
            <div className="right">
                <h2 className="title">{props.data.title}</h2>
                <p>{props.data.description}</p>
                <a href={props.data.link} target="_blank"> open in a new tab </a>
            </div>
            <style jsx>{`
                .cont{
                    width: 100%;
                    height: 400px;
                    display: grid;
                    grid-template-columns: 40% 40%;
                    grid-gap: 5%;
                    margin: 0;
                    margin-top: 50px;
                    padding: 0;
                }
                .left{
                    height: 100%;
                    overflow: hidden;
                    position: relative;
                }
                .left .overlay{
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: 0;
                    right: 0;
                    background: #ffffffb3;
                    display: flex;
                    cursor: pointer;
                    opacity: 0;
                    backdrop-filter: blur(15px);
                }
                .left .overlay:hover{
                    background: #000000b3;
                    opacity: 1;
                    transition-duration: 200ms;
                }
                .left .overlay img{
                    margin: auto;
                }
                .left .image {
                    width: 100%;
                    object-fit: cover;
                    object-position: top;
                    margin: 0;
                    padding: 0;
                }
                .title{
                    color: #fff;
                }
                a{
                    color: #ffff00;
                    text-decoration: none;
                }
            `}</style>
        </div>
    )
}
